import gsap from 'gsap';
import ScrollTrigger from 'gsap/dist/ScrollTrigger';
import { exist } from '../../utils';
import { OFFSETS } from '../../utils/constants';

const extractedAnimation = () => {
	const SELECTORS = {
		section: '.js-extracted-section',
		title: '.js-extracted-title',
		item: '.js-extracted-item',
		preview: '.js-extracted-preview',
		previewVerifyBtn: '.js-extracted-preview-verify-btn',
		verifyBtn: '.js-extracted-verify-btn',
		previewVerified: '.js-extracted-preview-title-verified',
		verified: '.js-extracted-title-verified',
		cursor: '.js-extracted-cursor',
		previewAvatars: '.js-extracted-preview-avatars',
		avatars: '.js-extracted-avatars',
	};

	const $section = document.querySelector(SELECTORS.section);

	if (!exist($section)) return;

	gsap.registerPlugin(ScrollTrigger);

	const $titles = $section.querySelectorAll(SELECTORS.title);

	const $preview = $section.querySelector(SELECTORS.preview);
	const $cursor = $section.querySelector(SELECTORS.cursor);
	const $previewVerifyBtn = $section.querySelector(SELECTORS.previewVerifyBtn);
	const $verifyBtn = $section.querySelectorAll(SELECTORS.verifyBtn);
	const $previewAvatars = $section.querySelectorAll(SELECTORS.previewAvatars);
	const $avatars = $section.querySelectorAll(SELECTORS.avatars);
	const $previewVerified = $section.querySelectorAll(SELECTORS.previewVerified);
	const $verified = $section.querySelectorAll(SELECTORS.verified);

	let mm = gsap.matchMedia();

	const startOffset = OFFSETS.power;
	const endOffset = OFFSETS.power + OFFSETS.extracted;

	const tl = gsap.timeline({
		paused: true,
		defaults: {
			ease: 'none',
		},
		scrollTrigger: {
			trigger: $section,
			start: () => (window.innerWidth < 1024 ? `top+=${startOffset} top` : `center+=${startOffset} center`),
			end: () => (window.innerWidth < 1024 ? `top+=${endOffset} top` : `center+=${endOffset} center`),
			pin: '.wrapper',
			scrub: true,
		},
	});

	mm.add('(min-width: 1024px)', () => {
		$titles.forEach((item, index) => {
			tl.to(item, {
				yPercent: -100,
				duration: 2,
			});

			if (index === 1) {
				tl.to(
					$preview,
					{
						opacity: 1,
						scale: 1,
						x: 0,
						y: 0,
						duration: 2,
					},
					'-=2',
				)
					.fromTo(
						$cursor,
						{
							opacity: 0,
						},
						{
							opacity: 1,
						},
					)
					.fromTo(
						$cursor,
						{
							x: 250,
							y: 40,
						},
						{
							x: 0,
							y: 0,
							duration: 2,
							ease: 'power2.out',
						},
					)
					.to($previewVerifyBtn, {
						scale: 1.2,
						duration: 1,
					})
					.to($previewVerifyBtn, {
						scale: 0,
						duration: 1.4,
					})
					.to($cursor, {
						opacity: 0,
						duration: 1,
					});
			}

			if (index === 2) {
				tl.to(
					$previewAvatars,
					{
						opacity: 1,
						stagger: 0.6,
					},
					'-=3',
				)
					.fromTo(
						$previewVerified,
						{
							opacity: 0,
							y: 15,
						},
						{
							opacity: 1,
							y: 0,
						},
						'<',
					)
					.to(
						{},
						{
							duration: 1,
						},
					);
			}

			if (index !== $titles.length - 1 && index !== 0) {
				tl.to(item, {
					// opacity: 0,
					yPercent: -200,
					duration: 2,
				});
			}
		});
	});

	mm.add('(max-width: 1023px)', () => {
		$titles.forEach((item, index) => {
			// tl.to(item, {
			// 	opacity: 1,
			// 	duration: 2,
			// });
			tl.to(item, {
				yPercent: -100,
				duration: 2,
			});

			if (index === 1) {
				tl.to($verifyBtn, {
					scale: 0,
					opacity: 0,
					duration: 1.4,
				})
					.to(
						$avatars,
						{
							opacity: 1,
							stagger: 0.2,
						},
						'<',
					)
					.fromTo(
						$verified,
						{
							opacity: 0,
							y: 15,
						},
						{
							opacity: 1,
							y: 0,
						},
						'<',
					);
			}

			if (index !== $titles.length - 1 && index !== 0) {
				tl.to(item, {
					// opacity: 0,
					yPercent: -200,
					duration: 2,
				});
			}
		});
	});

	// eslint-disable-next-line consistent-return
	return tl;
};

export default extractedAnimation;
